* {
  font-family: Roboto, Arial, sans-serif;
}

body {
  color: #555;
  scroll-behavior: smooth;
  background-color: #f4f7f6;
  margin: 0;
  padding: 0;
  line-height: 1.6;
}

section {
  padding-top: 200px;
}

h1, h2, h3, h4, h5, h6 {
  color: #2c3e50;
  margin-bottom: .5rem;
}

h1 {
  font-size: 2.8em;
}

h2 {
  font-size: 2.2em;
}

p {
  color: #555;
  margin-bottom: 1.5rem;
  font-size: 1.1em;
}

ul {
  list-style: none;
}

ul li a {
  color: #0056b3;
  text-decoration: none;
  transition: color .3s;
}

ul li a:hover, ul li a:focus {
  color: #555;
  text-decoration: underline;
}

article {
  color: #555;
  text-align: left;
  margin-top: 20px;
  padding: 0 20px;
  font-size: 1.15em;
  line-height: 1.8;
}

header {
  color: #ecf0f1;
  z-index: 1000;
  background-color: #2c3e50;
  padding: 1rem 0;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 5px #0000001a;
}

header .container {
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
  display: flex;
}

header .site-logo, footer .site-logo {
  width: 32px;
  height: 32px;
}

header h1 {
  color: #ecf0f1;
  margin: 0;
}

nav ul {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

nav ul li {
  margin: 0 1.5rem;
  display: inline-block;
}

nav ul li a {
  color: #ecf0f1;
  font-weight: bold;
  text-decoration: none;
  transition: color .3s ease-in-out;
}

nav ul li a:hover, nav ul li a:focus {
  color: #3498db;
}

#scroll-top-btn {
  z-index: 99;
  color: #fff;
  cursor: pointer;
  background-color: #0056b3;
  border: none;
  border-radius: 10px;
  outline: none;
  padding: 15px;
  font-size: 18px;
  display: none;
  position: fixed;
  bottom: 20px;
  right: 30px;
}

#scroll-top-btn:hover {
  background-color: #555;
}

.menu-toggle {
  color: #ecf0f1;
  cursor: pointer;
  font-size: 2rem;
  display: none;
}

main {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  max-width: 900px;
  margin: 2rem auto;
  padding: 2rem;
  box-shadow: 0 4px 10px #00000014;
}

img.rounded {
  object-fit: cover;
  border: 5px solid #3498db;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  transition: transform .3s ease-in-out;
  box-shadow: 0 4px 8px #00000026;
}

img.rounded:hover {
  transform: scale(1.05);
}

footer {
  text-align: center;
  background-color: #2c3e50;
  margin-top: 3rem;
  padding: 1.5rem 0;
  font-size: .9em;
}

footer p {
  color: #fff;
}

.portfolio-container {
  padding: 20px 0;
  overflow: hidden;
}

.portfolio-item {
  float: left;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  width: 30%;
  margin: 0 1.5% 30px;
  transition: transform .3s;
  box-shadow: 0 4px 8px #0000001a;
}

.portfolio-item:hover {
  transform: translateY(-5px);
}

.portfolio-item img {
  border-bottom: 1px solid #ddd;
  max-width: 100%;
  height: auto;
  margin: 0 auto;
  display: block;
}

.portfolio-item h2 {
  color: #333;
  margin: 15px 0 10px;
  font-size: 1.5rem;
}

.portfolio-item p {
  color: #666;
  text-align: left;
  padding: 0 15px 15px;
  font-size: .95rem;
}

.clear {
  clear: both;
  visibility: hidden;
  height: 0;
  display: block;
}

.intro-text {
  text-align: center;
  color: #555;
  margin-bottom: 40px;
  font-size: 1.1rem;
}

#contact {
  border-top: 1px solid #eee;
  padding-top: 100px;
  overflow: hidden;
}

.contact-form {
  float: left;
  box-sizing: border-box;
  width: 65%;
  padding-right: 20px;
}

div.contact-info {
  float: right;
  box-sizing: border-box;
  width: 35%;
  padding-left: 20px;
}

div.contact-info h2 {
  color: #333;
  margin-top: 0;
  margin-bottom: 15px;
  font-size: 1.4rem;
}

div.contact-info p {
  text-align: left;
  margin-bottom: 20px;
  font-size: .95rem;
}

div.contact-info ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

div.contact-info ul li {
  margin-bottom: 10px;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  color: #333;
  margin-bottom: 8px;
  font-weight: bold;
  display: block;
}

.form-group textarea {
  resize: vertical;
  min-height: 100px;
}

input, .form-group input[type="text"], .form-group input[type="email"], .form-group input[type="tel"], .form-group textarea {
  box-sizing: border-box;
  color: #555;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  padding: 12px;
  font-size: 1rem;
  transition: border-color .3s;
}

input:focus, .form-group input:focus, .form-group textarea:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 5px #007bff32;
}

.btn {
  color: #fff;
  background-color: #0056b3;
  border-radius: 5px;
  margin-bottom: 15px;
  padding: 10px 20px;
  text-decoration: none;
  transition: background-color .3s;
  display: inline-block;
}

.btn:hover {
  background-color: #555;
}

.submit-btn {
  color: #fff;
  cursor: pointer;
  background-color: #0056b3;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 12px 20px;
  font-size: 1.1rem;
  font-weight: bold;
  transition: background-color .3s, transform .2s;
  display: block;
}

.submit-btn:hover {
  background-color: #555;
  transform: translateY(-2px);
}

@media (width <= 768px) {
  nav ul {
    background-color: #2c3e50;
    flex-direction: column;
    width: 100%;
    padding: 1rem 0;
    display: none;
    position: absolute;
    top: 80px;
    left: 0;
  }

  nav ul.active {
    display: flex;
  }

  nav ul li {
    margin: 1rem 0;
  }

  .menu-toggle {
    display: block;
  }

  .portfolio-item, .contact-form, div.contact-info {
    float: none;
    width: 90%;
    margin: 0 auto 30px;
  }
}

.css-valid {
  border: 0;
  width: 88px;
  height: 31px;
}
